<template>
    <div class="container">
      <section id="readme">
        <h2>使用说明</h2>
        <div id="readme-content" v-html="markdownContent"></div>
      </section>
    </div>

    <footer>
      © 2025 我的 C++ HTTP 服务器网站
    </footer>
  </template>

  <script>
  import { ref, onMounted } from 'vue';
  import { marked } from 'marked';

  export default {
    name: 'ReadmeView',
    setup() {
      const markdownContent = ref('');

      onMounted(() => {
        fetch('/README_zh.md')
          .then(response => response.text())
          .then(markdown => {
            markdownContent.value = marked.parse(markdown);
          })
          .catch(error => console.error('Error fetching README.md:', error));
      });

      return {
        markdownContent
      }
    }
  }
  </script>