<template>
    <div class="container">
      <h1>联系我们</h1>
      <p>您可以通过以下方式与我们联系：</p>

      <section id="contact-info">
        <h2>联系信息</h2>
        <ul>
          <li><b>电子邮件：</b> contact@example.com</li>
          <li><b>电话：</b> +86</li>
          <li><b>地址：</b> 中国</li>
        </ul>
      </section>

      <section id="contact-form">
        <h2>联系表单</h2>
        <form @submit.prevent="handleSubmit" id="contactForm">
          <label for="name">姓名：</label><br>
          <input type="text" id="name" v-model="name"><br>
          <span id="nameError" class="error">{{ nameError }}</span><br>

          <label for="email">电子邮件：</label><br>
          <input type="email" id="email" v-model="email"><br>
          <span id="emailError" class="error">{{ emailError }}</span><br>

          <label for="message">留言：</label><br>
          <textarea id="message" v-model="message" rows="4"
            cols="50"></textarea><br>
          <span id="messageError" class="error">{{ messageError }}</span><br>

          <input type="submit" value="发送" class="button">
          <span id="formSuccess" class="success">{{ formSuccess }}</span>
          <span id="formError" class="error">{{ formError }}</span>
        </form>
      </section>

      <nav>
        <ul>
          <li><router-link to="/">首页</router-link></li>
          <li><router-link to="/about">关于</router-link></li>
          <li><router-link to="/services">服务</router-link></li>
          <li><router-link to="/contact">联系我们</router-link></li>
          <li><a href="https://www.bilibili.com/" class="button">了解更多</a></li>
        </ul>
      </nav>
    </div>

    <footer>
      © 2025 一个 C++ HTTP 服务器网站
    </footer>
  </template>

<script>
import { ref } from 'vue';
import { useRouter } from 'vue-router'; // 引入 useRouter

export default {
  name: 'ContactView',
  setup() {
    const router = useRouter(); // 使用 useRouter

    const name = ref('');
    const email = ref('');
    const message = ref('');
    const nameError = ref('');
    const emailError = ref('');
    const messageError = ref('');
    const formSuccess = ref('');
    const formError = ref('');

    const handleSubmit = async () => {
      nameError.value = '';
      emailError.value = '';
      messageError.value = '';
      formSuccess.value = '';
      formError.value = '';

      let isValid = true;

      if (name.value === '') {
        nameError.value = '姓名不能为空';
        isValid = false;
      }

      if (email.value === '') {
        emailError.value = '电子邮件不能为空';
        isValid = false;
      } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email.value)) {
        emailError.value = '电子邮件格式不正确';
        isValid = false;
      }

      if (message.value === '') {
        messageError.value = '留言不能为空';
        isValid = false;
      }

      if (!isValid) {
        return;
      }

      // 使用 fetch API 提交表单
      try {
        const response = await fetch('/contact', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          body: `name=${encodeURIComponent(name.value)}&email=${encodeURIComponent(email.value)}&message=${encodeURIComponent(message.value)}`
        });

        if (response.ok) {
          router.push({ name: 'contact-success' }); // 使用路由名称进行跳转
        } else {
          router.push({ name: 'contact-error', query: { status: response.status } });
        }
      } catch (error) {
        formError.value = `发生错误: ${error.message}`;
      }
    };

    return {
      name,
      email,
      message,
      nameError,
      emailError,
      messageError,
      formSuccess,
      formError,
      handleSubmit,
    };
  },
};
</script>
