import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import ContactView from '../views/ContactView.vue'
import ServicesView from '../views/ServicesView.vue'
import ReadmeView from '../views/ReadmeView.vue'
import ContactSuccess from '../views/ContactSuccess.vue' // 导入 ContactSuccess 组件
import ContactError from '../views/ContactError.vue'     // 导入 ContactError 组件

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
  {
    path: '/contact',
    name: 'contact',
    component: ContactView
  },
  {
    path: '/services',
    name: 'services',
    component: ServicesView
  },
  {
    path: '/readme',
    name: 'readme',
    component: ReadmeView
  },
  {
    path: '/contact_success',                 // 添加 ContactSuccess 路由
    name: 'contact-success',
    component: ContactSuccess
  },
  {
    path: '/contact_error',                   // 添加 ContactError 路由
    name: 'contact-error',
    component: ContactError,
    props: true  // 允许将路由参数作为 props 传递给组件
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router