<template>
    <div class="container">
      <h1>欢迎来到我的自定义 HTTP 服务器！</h1>
      <p>本网站由使用 Boost.Asio 编写的自定义高性能 C++ HTTP 服务器提供服务。 它专为速度、效率和集成 WebAssembly
        模块而设计。</p>
  
      <section id="server-info">
        <h2>服务器特性</h2>
        <ul>
          <li><b>自定义 C++ 实现：</b> 从头开始构建，以实现最佳的控制和性能。</li>
          <li><b>Boost.Asio 支持：</b> 利用异步 I/O 的强大功能来实现高并发。</li>
          <li><b>多线程：</b> 使用线程池来高效处理多个请求。（线程数：{{ threadCount }}）</li>
          <li><b>静态文件服务：</b> 高效地提供静态内容（HTML、CSS、JavaScript、图像）。</li>
          <li><b>WebAssembly (WASM) 支持：</b> 在客户端执行 WASM
            模块，允许在浏览器中执行计算密集型任务。</li>
          <li><b>可扩展：</b> 采用模块化设计，便于添加新功能和处理程序。</li>
        </ul>
      </section>
  
      <nav aria-label="main navigation">
        <ul>
          <li><router-link to="/">首页</router-link></li>
          <li><router-link to="/about">关于</router-link></li>
          <li><router-link to="/services">服务</router-link></li>
          <li><router-link to="/contact">联系我们</router-link></li>
          <li><a href="https://www.bilibili.com/" class="button">了解更多</a></li>
        </ul>
      </nav>
  
      <main>
        <section id="usage">
          <h2>使用方法</h2>
          <p>请参考以下文档：</p>
          <ul>
            <li><router-link to="/readme">使用说明</router-link></li>
          </ul>
          <img src="/asset/image.png" alt="示例图片" width="500" height="300">
  
        </section>
  
        <section id="wasm-section">
          <h2>WebAssembly 集成</h2>
          <p>本网站利用 WebAssembly 直接在您的浏览器中执行计算密集型任务。</p>
          <!-- 添加图片选择器 -->
          <label for="imageInput">选择图片：</label>
          <input type="file" id="imageInput" accept="image/*" title="选择图片文件" @change="handleImageChange">
          <p id="dynamicContent" ref="dynamicContent">灰度图将显示在此处。</p>
          <button class="button" id="wasmButton" :disabled="!imageSelected" @click="convertToGrayscale">转换成灰度图</button>
          <!--  默认禁用 -->
        </section>
      </main>
  
      <section id="extensions">
        <h2>潜在扩展</h2>
        <p>此服务器和网站可以通过多种功能进行扩展，包括：</p>
        <ul>
          <li><b>HTTPS 支持：</b> 使用 SSL/TLS 实现安全通信。</li>
          <li><b>动态内容生成：</b> 与后端数据库和脚本语言（例如 Python、Node.js）集成以生成动态内容。</li>
          <li><b>API 端点：</b> 创建 RESTful API 以供其他应用程序交互。</li>
          <li><b>服务器端 WASM：</b> 可能会在服务器端执行 WASM 以获得更高的性能（需要服务器端 WASM 运行时）。</li>
          <li><b>身份验证和授权：</b> 实施用户身份验证和授权以保护敏感资源。</li>
          <li><b>反向代理：</b> 充当反向代理以将流量分配到多个后端服务器。</li>
        </ul>
        <BeautifyComponent />
      </section>
    </div>
  
    <footer>
      © 2025 一个 C++ HTTP 服务器网站
    </footer>
  </template>
  
  <script>
  import { ref, onMounted } from 'vue';
  import BeautifyComponent from '../components/Beautify.vue';
  
  // 导入 scripts.js 中的函数和变量
  import {
    loadWasmModule,
    processImage,
    convertToGrayscale as convertToGrayscaleInternal  // 重命名，避免命名冲突
  } from '../scripts/scripts.js';
  
  // 导入 beautify.js 中的 beautify 函数
  import { initParticles } from '../scripts/beautify.js';
  
  export default {
    name: 'HomeView',
    components: {
      BeautifyComponent
    },
    setup() {
      const threadCount = ref(navigator.hardwareConcurrency || 4);
      const imageSelected = ref(false);
      const wasmInstance = ref(null);  // 保存 WASM 实例
      const rgbArray = ref(null); // 保存图像数据
      const dynamicContent = ref(null); // 引用 dynamicContent 元素
  
      const wasmModulePath = "/module.wasm";
  
      const handleImageChange = async (event) => {
        const file = event.target.files[0];
        if (file) {
          try {
            dynamicContent.value.innerText = "正在处理图片...";
            rgbArray.value = await processImage(file, dynamicContent.value); // 使用导出的 processImage
            imageSelected.value = true;
            dynamicContent.value.innerText = "";
          } catch (error) {
            console.error("图像处理失败:", error);
            dynamicContent.value.innerText = "图像处理失败: " + error.message;
            rgbArray.value = null;
            imageSelected.value = false;
          }
        } else {
          rgbArray.value = null;
          imageSelected.value = false;
          dynamicContent.value.innerText = "请选择一张图片。";
        }
      };
  
      const convertToGrayscale = async () => {
        if (!rgbArray.value) {
          dynamicContent.value.innerText = "请先选择图片！";
          return;
        }
  
        try {
          if (!wasmInstance.value) {
            wasmInstance.value = await loadWasmModule(wasmModulePath, dynamicContent.value); // 使用导出的 loadWasmModule
          }
  
          await convertToGrayscaleInternal(wasmInstance.value, rgbArray.value, dynamicContent.value); // 使用导出的 convertToGrayscale
        } catch (error) {
          console.error("WASM 执行过程中发生错误:", error);
          dynamicContent.value.innerText = error.message;
        }
      };
  
      onMounted(() => {
        initParticles();  // 初始化粒子效果
  
        // 设置线程数 (直接操作 DOM 元素是不推荐的，可以使用 ref 来绑定数据)
        // 查找thread-count元素
        const threadCountElement = document.getElementById("thread-count");
  
        if (threadCountElement) {
           threadCountElement.innerText = threadCount.value;
        } else {
          console.warn("未找到 threadCountElement 元素，无法显示线程数");
        }
      });
  
      return {
        threadCount,
        imageSelected,
        convertToGrayscale,
        handleImageChange,
        dynamicContent
      }
    }
  }
  </script>
  <style src="@/styles/style.css"></style>